import { mapGetters } from 'vuex';
export default {
  computed: {
    ...mapGetters(['userConfig', 'useDarkMode'])
  },
  watch: {
    useDarkMode() {
      this.setSiteIcon();
    },
    '$i18n.locale'() {
      this.setSiteTitle();
      this.setSiteIcon();
    }
  },
  methods: {
    setSiteTitle() {
      const siteTitleKey = this.userConfig?.siteTitleI18n || 'Halier ImgHub';
      document.title = this.$t(siteTitleKey) || siteTitleKey;
    },
    setSiteIcon() {
      // 移除旧的 favicon
      const existingLink = document.querySelector('link[rel="icon"]');
      if (existingLink) {
        document.head.removeChild(existingLink);
      }
      const link = document.createElement('link');
      link.rel = 'icon';

      // 根据深色模式和语言选择不同的 logo
      let iconPath = '/logo.png';
      if (this.useDarkMode) {
        iconPath = this.userConfig?.siteIconDark || '/logo-dark.png';
      } else {
        iconPath = this.userConfig?.siteIcon || '/logo.png';
      }
      link.href = iconPath;
      document.head.appendChild(link);
    }
  },
  mounted() {
    // 初始化标题
    this.setSiteTitle();
  }
};